					<span class="mui-icon iconfont icon-meili"></span>
					<span class="mui-tab-label"></span>
				</div>
				<div class="content">

				</div>
			</div>

</div>
</template>
<script>
import Menu from '@/pages/Index';
export default {
  components: {
    Menu
  },
  data() {
    return {
      // +3
			top3CharmUsers: [
				{ nickname: 'Telanx', qq: '1241818518', zan_amt: '12,00000', fan_amt: '3000' },
				{ nickname: 'Ceko', qq: '851656783', zan_amt: '12,00000', fan_amt: '3000' },
				{ nickname: '', qq: '851656785', zan_amt: '12,00000', fan_amt: '3000' },
			]
    };
  }
}
</script>


